import React, {useEffect, useState} from 'react';
import { H2, Div, TD, TR, TBody } from 'styled-system-html'
import styled, {css} from 'styled-components';
import paginate from 'paginate-array';
import {delay} from '../commons/helper';
import jsonQuery from 'json-query';

const columns = {
	"id": {
		"label": "ID"
	},
	"viet_name": {
		"label": "Vietnamese Name"
	},
	"eng_name": {
		"label": "English Name"
	}
}




const display = 100;
const columnsKey = Object.keys(columns);
let page = 1;
let field = {
	search: ""
};

export default (props) => {
	const [data, setData] = React.useState([]);
	const [filteredData, setFilteredData] = React.useState([]);
	const [items, setItems] = React.useState({
		currentPage: 1,
		perPage: 100,
		total: 100,
		totalPages: 1,
		data: []
	});

	const nextPage = async (page, paginateData) => {
		const paginateItems = paginate(paginateData, page, display);
		await setItems(paginateItems);
		console.log(paginateItems);
	}

	const fetchData = () => {
		fetch('./static/datas/map.json')
		.then((res) => res.json())
		.then(async (res) => {
			await setData(res);
			nextPage(1, res);
			setFilteredData(res);
		})
	}

	const onSearch = (e) => {
		if(e.key == 'Enter'){

			const searchValue = field.search;

			if(!searchValue) {
				nextPage(1, data);
				return true;
			}

			var helpers = {
				contains: function (input, arg) {
					input = new String(input);
					input = input.normalize("NFD").replace(/[\u0300-\u036f]/g, "").toLowerCase();
					arg = arg.normalize("NFD").replace(/[\u0300-\u036f]/g, "").toLowerCase();
					let test = input.includes(arg)
					return test;
				}
			}

			const query = jsonQuery(`[*
				eng_name:contains(${searchValue}) |
				viet_name:contains(${searchValue}) |
				id:contains(${searchValue})
			]` , {
				data:  data,
				locals: helpers
			})
			console.log(query, searchValue);
			nextPage(1, query.value);
			setFilteredData(query.value);
		}
	}

	const updateField = (e, key) => {
		field[key] = e.target.value;
	}

	useEffect(() => {
		fetchData();
	}, []);

	return(
	<Wrapper>
		<H2 mb={2}>Map</H2>
		<StickContainer>
			<Search type="text" placeholder="Search(ID, English Name, Vietnamese Name)" onChange={(e) => updateField(e, 'search')} onKeyDown={onSearch} /> 

			<Pagination>
				{page > 1 &&
					<Button onClick={() => nextPage(--page, filteredData)}>
						Prev
					</Button>
				}
				<Div>{page}</Div>
				{page * display < data.length &&
					<Button onClick={() => nextPage(++page, filteredData)}>
						Next
					</Button>
				}
				
			</Pagination>
		</StickContainer>
		<Table border="solid 1px gray1" width={1}>
			<THead bg="gray1">
				<TR>
					{columnsKey.map((item, key) => (
						<TH p={1} key={key}>{columns[item].label}</TH>
					))}
				</TR>
			</THead>
			<TBody borderBottom="1px solid" borderColor="gray2">
					{(!items.data.length ? <TR><TD colSpan={columnsKey.length}>...</TD></TR>
						:
						(items.data.map((item, key) => (
							<ItemTr key={key} borderTop="1px solid" borderColor="gray2">
								{columnsKey.map((object, key2) => (
									<ItemTd key={key2}>{item[object]}</ItemTd>
								))}
							</ItemTr>
						)))

					)}
			</TBody>
		</Table>
		
	</Wrapper>
	)
}

const Wrapper = styled.div`
	padding: 20px 10px;
`

const Table = styled.table`
	display: block;
	width: 100%;
	overflow: auto;
`

const TH = styled.th`
	padding: 10px;
	border: 1px solid #444;
`


const THead = styled.thead`
`

const ItemTr = styled.tr`
	
`

const ItemTd = styled.td`
	padding: 10px;
	border: 1px solid #444;


	${ItemTr}:hover & {
    background: #000;
  }
`

const Search = styled.input`
	width: 100%;
	margin: 0 0 20px;
	
`

const StickContainer = styled.div`
	position: -webkit-sticky;
	position: sticky;
	top: 0;
	left: 0;
`

const Button = styled.button`
	margin: 0 10px;
`

const Pagination = styled.div`
	display: flex;
	justify-content: flex-end;
`


