import { Flex, Box } from 'grid-styled'
import { Div, Nav, Img, Span } from 'styled-system-html';
import Link from 'next/link'
import SiteNavLink from './SiteNavLink'
import SiteNavLinkExternal from './SiteNavLinkExternal'

export default (props) => (
    <Div bg="base"  zIndex="9999" width={1}>
        <Nav bg="rgba(0,0,0,.2)" textAlign='left' px={[2,0,2]}>
            <Flex flexWrap="wrap" fontSize={[1,2]}>
                <SiteNavLink url="/" isCurrent={props.current === 'Index'}>NPC</SiteNavLink>
                <SiteNavLink url="/npcmap" isCurrent={props.current === 'NpcMap'}>NPC in Map</SiteNavLink>
                <SiteNavLink url="/item" isCurrent={props.current === 'Item'}>Item</SiteNavLink>
                <SiteNavLink url="/map" isCurrent={props.current === 'Map'}>Map</SiteNavLink>
            </Flex>
        </Nav>
    </Div>
)


